.trendsHeader {
  font-size: 50px;
}

.trendsList {
  margin-top: 1rem;
  max-width: fit-content;
  position: relative;
}

.trendsListItem {
  display: flex;
  align-items: center;
  background: white;
  border-radius: 0 0.5rem 0.5rem 0.5rem;
  box-shadow: 0.25rem 0.25rem 0.6rem rgba(0, 0, 0, 0.05),
    0 0.5rem 1.125rem rgba(75, 0, 0, 0.05);
  counter-increment: gradient-counter;
  margin-top: 30px;
  min-height: 50px;
  padding-left: 50px;
  padding-right: 10px;
  position: relative;
}

.trendsListItem::after,
.trendsListItem::before {
  background: linear-gradient(135deg, #83e4e2 0%, #a2ed56 100%);
  border-radius: 1rem 1rem 0 1rem;
  content: '';
  height: 3rem;
  left: -1rem;
  overflow: hidden;
  position: absolute;
  top: -1rem;
  width: 3rem;
}

.trendsListItem::before {
  align-items: flex-end;
  box-shadow: 0.25rem 0.25rem 0.6rem rgba(0, 0, 0, 0.05),
    0 0.5rem 1.125rem rgba(75, 0, 0, 0.05);
  content: counter(gradient-counter);
  color: #1d1f20;
  display: flex;
  font: 900 1.5em/1 'Montserrat';
  justify-content: flex-end;
  padding: 0.125em 0.25em;
  z-index: 1;
}

.trendsListItemLink {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}
